import Vue from 'vue'
import dateFormat from 'vux/src/tools/date/format.js'


function amount (n) {
n = (n / 100).toFloor(2)
var num = String(n).split('.')
return num[0].split('').reverse().map((item, index, array) => {
if (++index !== array.length && index % 3 === 0) {
return ',' + item
}
return item
}).reverse().join('') + (num[1] ? "." + num[1] : "")
}
function date (v) {
return dateFormat(new Date(v), 'YYYY-MM-DD')
}
function dateCh (v) {
return dateFormat(new Date(v), 'YYYY年MM月DD日')
}
function dateDot (v) {
return dateFormat(new Date(v), 'YYYY.MM.DD')
}
function dateDif (v) {
var n = (new Date(v) - new Date().getTime()) / 86400000;
if (n <= 0) {
n = 0;
} else {
n = Math.ceil(n);
}
return n;
}
function dateHFM (v) {
return dateFormat(new Date(v), 'YYYY-MM-DD HH:mm:ss')
}
function dateChange (v) {
if (typeof v == 'number') {
v = v.toString();
} else if (typeof v == 'string') {
v = v;
} else if (v == null) {
return
}
var n = v.indexOf('.');
if (n > 0) {
var arrlen = v.slice(n).length;
if (arrlen == 1) {
v = v + '00';
} else if (arrlen == 2) {
v = v + '0';
}
} else if (n == -1) {
v = v + '.00';
}
return v;
}
function telChange (str) {
if (str) {
var n = str.slice(3, 9);
var str1 = str;
str = str1.replace(n, '******');
return str;
}
}
function cardID (str) {
var end = str.toString().substr(-4); 
var string = '';
for (var i = 0; i < str.length - 5; i++) {
string += '*'
}
return string + end;
}
function card (card) {
var card = card || null;
if (card == null) {
return;
}
var end = card.toString().substr(-4);
var start = card.toString().substr(0, 4);
var str1 = '';
for (let i = 1; i <= 8; i++) {
if (i % 5 == 0) {
str1 += " *";
} else {
str1 += "*"
}
}
return start + " " + str1 + " " + end;
}
function star (v) {
if (!v) {
return;
}
var firstName = v.charAt(0);
for (let i = 1; i < v.length; i++) {
firstName += "*"
}
return firstName;
}
function formatCurrent (res) {
let result = res || null;
let val = '';
if (result == null) {
result = 0;
} else {
result = result - 0;
}
let refVal = 10000000;

if (result < refVal) { //小于10万
result = ('' + amount(result)).split('.')[0];
val = result + ""
} else {     //大于10万
result = amount(result * 1000 / refVal); 
val = result + "";
}
return val;
}
function cardCouDis (djq) {
let res;
if (djq == null || isNaN(djq)) {
return;
}
return res = djq / 100 + "";
}
function dateSliceToDot (v) {
return v.substr(0,4) + '.' + v.substr(4,2) + '.' + v.substr(6,2)
}
function formatStatus (v) {
let status
switch(v) {
case '0':
status = '待收款';
break;
case '1':
status = '已收款';
break;
case '2':
status = '逾期';
break;
case '3':
status = '提前还款';
break;
case '4':
status = '代偿';
break;
default:
status = '-';
break;
}
return status
}
var filter = {
amount,
date,
dateCh,
dateDot,
dateDif,
dateHFM,
dateChange,
telChange,
cardID,
card,
star,
formatCurrent,
cardCouDis,
dateSliceToDot,
formatStatus
}
for (var key in filter) {
Vue.filter(key, filter[key])
}

export default filter


