import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [
  {
    path: '/', // 临时
    name: 'linshi',
    component: resolve => require(['../pages/linshi'], resolve),
    meta: {requiresAuth: true},
  },
  {
    path: '/registered', // 注册
    name: 'registered',
    component: resolve => require(['../pages/registered'], resolve),
    meta: {requiresAuth: true, title: '注册'},
  },
  {
    path: '/login',  // 登陆
    name: 'login',
    component: resolve => require(['../pages/login'], resolve),
    meta: {requiresAuth: true, title: '账号关联'},
  },
  {
    path: '/verified',  // 用户设置
    name: 'verified',
    component: resolve => require(['../pages/verified'], resolve),
    meta: {requiresAuth: true, title: '用户设置'},
  },
  {
    path: '/passwordmanage',  // 密码管理
    name: 'passwordmanage',
    component: resolve => require(['@/pages/mine/password/PasswordManage'], resolve),
    meta: {requiresAuth: true},
  },
  {
    path: '/forgetpassword',  // 忘记密码
    name: 'forgetpassword',
    component: resolve => require(['@/pages/mine/password/ForgetPassword'], resolve),
    meta: {requiresAuth: true},
  },
  {
    path: '/passwordmodify',  // 密码修改
    name: 'passwordmodify',
    component: resolve => require(['@/pages/mine/password/PasswordModify'], resolve),
    meta: {requiresAuth: true},
  },
  {
    path: '/passwordmodified',  // 密码修改成功
    name: 'passwordmodified',
    component: resolve => require(['@/pages/mine/password/PasswordModified'], resolve),
    meta: {requiresAuth: true},
  },
]

var setDocumentTitle = function (title) {
  document.title = title;
  if (/ip(hone|od|ad)/i.test(navigator.userAgent)) {
    var i = document.createElement('iframe');
    i.src = '/favicon.ico';
    i.style.display = 'none';
    i.onload = function () {
      setTimeout(function () {
        i.remove();
      }, 9)
    }
    document.body.appendChild(i);
  }
}

const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  console.log(to, 11111)
  console.log(from, 22222)
  next()
})

export default router



