function KEEP(_,cb){cb();}
function mulFloat(arg1, arg2) {
  var m = 0;
  var s1 = arg1.toString();
  var s2 = arg2.toString();
  try {
    m += s1.split(".")[1].length
  } catch (e) {
  }
  try {
    m += s2.split(".")[1].length
  } catch (e) {
  }
  return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
}

Object.assign(Number.prototype, {
  toFloor(num) {
    return (Math.floor(mulFloat(this, Math.pow(10, num))) / Math.pow(10, num)).toFixed(num);
  },
  add(num) {
    var s1 = this.toString();
    var s2 = num.toString();
    //m=Math.max()
    var len1 = (s1.split(".")[1] || "").length
    var len2 = (s2.split(".")[1] || "").length
    s1 = s1.replace(".", "")
    s2 = s2.replace(".", "")
    var a = String(Math.pow(10, Math.abs(len2 - len1))).substr(1);
    var m = len1;
    if (len1 > len2) {
      s2 += a;
    } else { //相等和小于时逻辑相同
      m = len2
      s1 += a;
    }
    return (Number(s1) + Number(s2)) / Math.pow(10, m)
  },
  mul(num) {
    return mulFloat(this, num)
  }
})

String.prototype.format = function () {
  var args = Array.prototype.slice.call(arguments);
  var count = 0;
  return this.replace(/%s/g, function () {
    return args[count++];
  });
}
Array.prototype.clone = function () {
  return JSON.parse(JSON.stringify(this));
}

function dateSliceToDot(v) {
  return v.substr(0,4) + '.' + v.substr(4,2) + '.' + v.substr(6,2)
}
/**
 * 判断日期是否为最后一天
 * @param {*} y 年
 * @param {*} m 月
 * @param {*} d 日
 */
function isLastdy(y,m,d) {
  var res
  var m1 = {
    monthes: [1,3,5,7,8,10,12],
    dynum: 31
  }
  var m2 = {
    monthes: [2],
    dynum: 28
  }
  var m3 = {
    monthes: [4,6,9,11],
    dynum: 30
  }

  if((y%4 == 0 && y%100 != 0) || y%400 == 0) { // 闰年
    m2.dynum = 29
  }

  var mes = []
  mes.push(m1,m2,m3)
  
  var res = mes.find(el => el.monthes.includes(m-0)).dynum == d
  if(res) {
    return true
  }else {
    return false
  }
}

function syncData(data) {
  if (data.authKey) {
    common.authKey = data.authKey
  }
  if (data.cookie) {
    eval(data.cookie);
  } else {
    document.cookie = "authKey=" + common.authKey + ";path=/;";
  }
}

var helper = {
  makePath(module, path) {
    return location.href.replace(/(.*)(\/.*?)\.html(.*)/, "$1/" + module + ".html#" + path);
  },
  isLogin() {
    return !!common.authKey
  },
  setLogin(authKey) {
    common.authKey = authKey;
  }
}

//设置全局变量、方法、函数
function log() {
  var arg = arguments;
  KEEP("!prod", () => {
    console.log.apply(console, arg);
  })
}

window.common = navigator.userAgent.split('###')[2];
if (common) {
  isApp = true;
  //log("")
  common = JSON.parse(common);
} else {
  common = {}
}


