

import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import store from './store'
import router from './router/wechar.js'
import VueScroller from 'vue-scroller'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import './common/js/common.js'
import './common/js/filter.js'
import './common/js/http.js'
import './common/js/extension.js'
import 'lib-flexible'
import './common/js/init'

Vue.use(Vuex)
Vue.use(ToastPlugin)
Vue.use(VueScroller)

Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})



