import Vue from 'vue'

var fly = require('flyio')
var path = location.protocol + "//" + location.host + "/";

fly.config.timeout = 15000
if (location.host.indexOf('localhost') >= 0) {
  fly.config.baseURL = path + "api";
} else {
  fly.config.baseURL = path + "m-api";
}
fly.interceptors.request.use((config, promise) => {
  config.headers.apiVersion = '1.2';
  config.headers.os = 'Android'
  return config
})
window.fly = fly;
fly.interceptors.response.use(
  (response, promise) => {
    console.log(response.engine.getResponseHeader('Content-Type'))
    return response.data
    // return 123
  },
  (err, promise) => {
    //发生网络错误后会走到这里
  }
)
Vue.prototype.$http = Vue.http = fly;
export {fly}



